    -webkit-transition: all .3s ease-in-out 0s;
    transition: all .3s ease-in-out 0s;
    border: 1px solid #4d4d4d;
}
.lookbook .view-archive-button:hover {
      color: #fff !important;
      border-color: #000;
      background-color: #000;
}














































































.lookbook-item {
  position: relative;
}
.lookbook-item:hover:after {
    -webkit-transition: opacity .15s ease 0s;
    transition: opacity .15s ease 0s;
    opacity: 1;
}
.lookbook-item:hover .product-list-wrapper {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
.lookbook-item:after {
    width: 100%;
    top: 0;
    -webkit-transition: opacity .22s ease .22s;
    transition: opacity .22s ease .22s;
    position: absolute;
    opacity: 0;
    left: 0;
    height: 100%;
    display: block;
    content: "";
    background: none repeat scroll 0 0 rgba(0, 0, 0, 0.35);
}
.lookbook-item .product-list-wrapper {
    z-index: 1000;
    top: 0;
    -webkit-transition: all linear .2s;
    transition: all linear .2s;
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    right: -1px;
    position: absolute;
    overflow: hidden;
    opacity: 0;
    left: 0;
    color: rgba(255, 255, 255, 0.8);
    bottom: -1px;
}
.lookbook-item .product-list-wrapper .product-item {
      color: rgba(255, 255, 255, 0.8);
}
.lookbook-item .product-list-wrapper .product-item:hover {
        color: #fff;
}
.lookbook-item hr {
    border-top: 2px solid #f6f5f5;
}
.lookbook-item a:hover {
    text-decoration: none;
}














































